<template>
  <el-dialog  :append-to-body="true" :close-on-click-modal="false"  title="发送消息" :visible.sync="visible" width="70%" style="margin-top: 20px;">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">


      <el-form-item label="内容" prop="content">
        <el-input type="name" v-model="ruleForm.content"  ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">发送</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>

</template>

<script>


  export default {

    data() {
      return {
        ruleForm: {
          name: '',
          content:''
        },
        rules: {
          content: [
            { required: true, message: '请填写发送内容', trigger: 'change' }
          ],
        },
        visible:false,
        students:[],
        rowData:{},
        user_type:''
      };
    },
    methods: {
      initForm(){

      },
      edit:function(data){
        this.rowData=Object.assign(this.rowData, data)
        console.log(908)
        console.log(this.rowData)
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              row_data:this.rowData,
              content:this.ruleForm.content,
              user_type:this.user_type
            }
            var url = "http://www.myteach.com/api/replyMessage";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }else{
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
                this.$emit('func',{})
              }
            }).catch((error)=> {
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },

      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      selectUserChange(){

      }
    },
  }
</script>

