
<template>
    <div id="header" >
        <ul >
            <li> <a v-if="!token"  href="/#/register">教师管理员注册</a></li>
            <li> <a v-if="!token"  href="/#/login">登录</a></li>
            <li> <a v-if="token" @click="logout" >退出</a></li>
        </ul>
    </div>
</template>
<script>
    export default {
        name:'TheHeader',
        data(){
            return {
                token:'',
            }
        },
        methods:{
            logout(){
                var url = "http://www.myteach.com/api/logout";
                this.axios.post(url).then((res)=> {
                    console.log(res)
                    this.loading = false;
                    if (res.data.code != 200) {
                        this.$message({
                            message: res.data.msg,
                            type: "error"
                        });
                        return false;
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: "success"
                        });
                        localStorage.removeItem('teach_user_token')
                    }
                }).catch((error)=> {
                    this.loading = false;
                    console.log(error);
                });
            }
        },
        mounted() {
            this.token= localStorage.getItem('teach_user_token');

        }

    }
</script>
<style scoped>
    .title {
        display: none;
    }
    ul{
        list-style: none;
    }

    .navbar-default .navbar-nav > .active > a { background: rgba(0,0,0,.03);}
</style>



