<template>
  <div class="hello">

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <!--<el-tab-pane label="教师管理员登录" name="first">-->
        <!--<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">-->
          <!--<el-form-item label="邮箱" prop="email">-->
            <!--<el-input type="email" v-model="ruleForm.email" autocomplete="off"></el-input>-->
          <!--</el-form-item>-->
          <!--<el-form-item label="密码" prop="password">-->
            <!--<el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>-->
          <!--</el-form-item>-->
          <!--<el-form-item>-->
            <!--<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>-->
          <!--</el-form-item>-->
        <!--</el-form>-->
      <!--</el-tab-pane>-->
      <el-tab-pane label="教师登录" name="first">
        <el-form :model="ruleForm1" status-icon :rules="rules1" ref="ruleForm1" label-width="100px" class="demo-ruleForm">
          <el-form-item label="邮箱" prop="email">
            <el-input type="email" v-model="ruleForm1.email" ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm1.password" ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm1')">提交</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="学生登录" name="second">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
          <el-form-item label="姓名" prop="name">
            <el-input type="name" v-model="ruleForm2.name" ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm2.password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>


  </div>
</template>

<script>
  import  storage from  '../utils/storage.js'
  export default {
    data() {
      return {
        activeName: 'first',
        ruleForm1: {
          email: '',
          password:''
        },
        ruleForm2: {
          name: '',
          password:''
        },
        rules1: {
          email: [
            { required: true, message: '请填写邮箱', trigger: 'change' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'change' }
          ],
        },
        rules2: {
          name: [
            { required: true, message: '请填写姓名', trigger: 'change' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'change' }
          ],
        }
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },

      submitForm(formName) {
        if(formName=='ruleForm1'){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              var param={
                email:this.ruleForm1.email,
                password:this.ruleForm1.password,
                type:'teacher'
              }
              var url = "http://www.myteach.com/api/login";
              this.axios.post(url, param).then((res)=> {
                console.log(res)
                if (res.data.code != 200) {
                  this.$message({
                    message: res.data.msg,
                    type: "error"
                  });
                  return false;
                }else{
                  storage.set('teach_user_token','Bearer '+res.data.data.access_token)
                  this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  // window.open('http://localhost:8080/#/teachers')
                  this.getUserInfo()
                }
              }).catch((error)=> {
                console.log(error);
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }

        if(formName=='ruleForm2'){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              var param={
                name:this.ruleForm2.name,
                password:this.ruleForm2.password,
                type:'student'
              }
              var url = "http://www.myteach.com/api/login";
              this.axios.post(url, param).then((res)=> {
                console.log(res)
                if (res.data.code != 200) {
                  this.$message({
                    message: res.data.msg,
                    type: "error"
                  });
                  return false;
                }else{
                  storage.set('teach_user_token','Bearer '+res.data.data.access_token)
                  this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  window.open('http://localhost:8080/#/students')
                }
              }).catch((error)=> {
                console.log(error);
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }

      },
      getUserInfo(){

        var url = "http://www.myteach.com/api/user";
        this.axios.get(url).then((res)=> {
          if (res.data.code != 200) {
            return false;
          }else{
            this.userInfo=res.data.data
            if(this.userInfo.role=='teacher_admin'){
              window.open('http://localhost:8080/#/teacherAdmins')
            }
            if(this.userInfo.role=='teacher'){
              window.open('http://localhost:8080/#/teachers')
            }

          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



