<template>
  <el-dialog  :close-on-click-modal="false"  title="添加学校" :visible.sync="visible" width="70%" style="margin-top: 20px;">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <el-form-item label="学校名称" prop="name">
        <el-input type="name" v-model="ruleForm.name"  ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>

</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
        },
        rules: {
          name: [
            { required: true, message: '请填写学校名称', trigger: 'change' }
          ],
        },
        visible:false
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              name:this.ruleForm.name
            }
            var url = "http://www.myteach.com/api/addSchool";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }else{
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
                this.$emit('func',{})
              }
            }).catch((error)=> {
              this.loading = false;
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



