<template>
  <el-dialog  :append-to-body="true" :close-on-click-modal="false"  title="发送消息" :visible.sync="visible" width="70%" style="margin-top: 20px;">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <el-form-item label="接收用户" prop="user_id">
        <el-select  @change="selectUserChange"  type="name" v-model="ruleForm.user_id">
          <el-option
                  v-for="item in users"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="内容" prop="content">
        <el-input type="name" v-model="ruleForm.content"  ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">发送</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>

</template>

<script>


  export default {

    data() {
      return {
        ruleForm: {
          name: '',
          user_id:''
        },
        rules: {
          user_id: [
            { required: true, message: '请选择用户', trigger: 'change' }
          ],
          content: [
            { required: true, message: '请填写发送内容', trigger: 'change' }
          ],
        },
        visible:false,
        users:[],
        send_type:'',
        receive_type:''
      };
    },
    methods: {
      initData(){
        this.users=[]
        this.ruleForm={
          name: '',
          user_id:''
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              user_id:this.ruleForm.user_id,
              content:this.ruleForm.content,
              send_type:this.send_type,
              receive_type:this.receive_type
            }
            var url = "http://www.myteach.com/api/addMessage";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }else{
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
                this.$emit('func')
              }
            }).catch((error)=> {
              this.loading = false;
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      getStudentsByUserId(){
        var param = {
        };
        var url = "http://www.myteach.com/api/getStudentsByUserId";
        this.axios.get(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.users=res.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getTeachersByUserId(){
        var param = {
        };
        var url = "http://www.myteach.com/api/getTeachersByUserId";
        this.axios.get(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.users=res.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      selectUserChange(){

      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



