<template>
  <el-dialog  :close-on-click-modal="false"  title="添加学生" :visible.sync="visible" width="70%" style="margin-top: 20px;">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <el-form-item label="姓名" prop="name">
        <el-input type="name" v-model="ruleForm.name"  ></el-input>
      </el-form-item>

      <el-form-item label="所属学校" prop="school_id">
        <el-select     type="name" v-model="ruleForm.school_id">
          <el-option
                  v-for="item in schools"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>

</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
        },
        rules: {
          name: [
            { required: true, message: '请填写学生名称', trigger: 'change' }
          ],
          school_id: [
            { required: true, message: '请选择学校', trigger: 'change' }
          ],
        },
        visible:false,
        schools:''
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              name:this.ruleForm.name,
              school_id:this.ruleForm.school_id
            }
            var url = "http://www.myteach.com/api/addStudent";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }else{
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
                this.$emit('func',{})
              }
            }).catch((error)=> {
              this.loading = false;
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      getSchoolsByUserId(){
        var param = {
        };
        var url = "http://www.myteach.com/api/getSchoolsByUserId";
        this.axios.get(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.schools=res.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    mounted() {
      this.getSchoolsByUserId()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



