<template>
  <div class="hello">
    <el-tag  @click="openMessageList">你有待收消息:{{show_num}}条</el-tag>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">

      <el-tab-pane label="关注的人" name="first">
        <el-table
                :data="studentData"
                style="width: 100%">
          <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="name"
                  label="所属学校">
          </el-table-column>
          <el-table-column
                  prop="created_at"
                  label="加入日期">
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
                  @size-change="handleSizeChangeStudent"
                  @current-change="handleCurrentChangeStudent"
                  :current-page="currentPageStudent"
                  :page-sizes="[10, 50, 100, 500, 1000]"
                  :page-size="pageSizeStudent"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="pageTotalStudent">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>

    <TeacherAdd  ref="teacherAdd"></TeacherAdd>
    <SchoolAdd  ref="schoolAdd"></SchoolAdd>
    <StudentAdd  ref="studentAdd"></StudentAdd>
    <MessageList  ref="messageList"></MessageList>
  </div>
</template>

<script>
  import TeacherAdd from './TeacherAdd'
  import SchoolAdd from './SchoolAdd'
  import StudentAdd from './StudentAdd'
  import MessageList from './MessageList'
  import storage from '../utils/storage'

  export default {
    components: {
      // 局部注册 TheSidebar
      TeacherAdd,
      SchoolAdd,
      StudentAdd,
      MessageList
    },
    data() {
      return {
        activeName: 'first',
        pageSizeTeacher:10,
        currentPageTeacher:1,
        pageTotalTeacher:0,
        pageSizeSchool:10,
        currentPageSchool:1,
        pageTotalSchool:0,
        pageSizeStudent:10,
        currentPageStudent:1,
        pageTotalStudent:0,
        teacherData:[],
        schoolData:[],
        studentData:[],
        show_num:0,
        token:'',
        userInfo:[]
      }
    },
    methods: {
      handleSizeChangeTeacher:function(val) {
        this.pageSizeTeacher = val;
        this.getTeacherData();
      },
      handleCurrentChangeTeacher:function(val) {
        this.currentPageTeacher = val;
        this.getTeacherData();
      },
      handleSizeChangeSchool:function(val) {
        this.pageSizeSchool = val;
        this.getSchoolData();
      },
      handleCurrentChangeSchool:function(val) {
        this.currentPageSchool = val;
        this.getSchoolData();
      },
      handleSizeChangeStudent:function(val) {
        this.pageSizeStudent = val;
        this.getStudentData();
      },
      handleCurrentChangeStudent:function(val) {
        this.currentPageStudent = val;
        this.getStudentData();
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      addTeacher(){
        this.$refs.teacherAdd.visible=true
      },
      addSchool(){
        this.$refs.schoolAdd.visible=true
      },
      addStudent(){
        this.$refs.studentAdd.visible=true
      },
      openMessageList(){
        this.$refs.messageList.visible=true
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              email:this.ruleForm.email
            }
            var url = "http://www.myteach.com/api";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }
            }).catch((error)=> {
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      getTeacherData(){
        var param = {
          pageSize: this.pageSizeTeacher,
          page: this.currentPageTeacher,
        };
        var url = "http://www.myteach.com/api/getTeacherData";
        this.axios.post(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeTeacher=res.data.data.pageSize
            this.pageTotalTeacher = res.data.data.total
            this.teacherData=res.data.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getSchoolData(){
        var param = {
          pageSize: this.pageSizeSchool,
          page: this.currentPageSchool,
        };
        var url = "http://www.myteach.com/api/getSchoolData";
        this.axios.post(url, param).then((res)=> {
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeSchool=res.data.data.pageSize
            this.pageTotalSchool = res.data.data.total
            this.schoolData=res.data.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getStudentData(){
        var param = {
          pageSize: this.pageSizeStudent,
          page: this.currentPageStudent,
        };
        var url = "http://www.myteach.com/api/getStudentData";
        this.axios.post(url, param).then((res)=> {
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeStudent=res.data.data.pageSize
            this.pageTotalStudent = res.data.data.total
            this.studentData=res.data.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getUserInfo(){

        var url = "http://www.myteach.com/api/user";
        this.axios.get(url).then((res)=> {
          if (res.data.code != 200) {
            return false;
          }else{
            this.userInfo=res.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      websocketMessage(){
        setTimeout(()=>{
          console.log(this.userInfo)
          //创建一个webSocket 实例
          var ws = new WebSocket("ws://127.0.0.1:2000");
          var uid=this.userInfo.id
          ws.onopen = function(){


            var token= localStorage.getItem('teach_user_token');
            var params ={
              uid:uid,
              type:'student'
            };
            var data=JSON.stringify(params)
            ws.send(data);
          };

          ws.onmessage = (e)=>{
            var res=JSON.parse(e.data);
            console.log(1111);
            console.log(res.data.show_num);
            this.show_num=res.data.show_num
          };
        },2000)




      }
    },

    mounted() {
      this.getUserInfo()
      this.getTeacherData()
      this.getSchoolData()
      this.getStudentData()

      this.websocketMessage()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



