<template>
  <el-dialog  :close-on-click-modal="false"  title="消息列表" :visible.sync="visible" width="70%" style="margin-top: 20px;">

    <el-form   :inline="true" class="form-inline" @submit.native.prevent>
      <el-form-item>
        <el-button type="primary" @click="messageAddStudent()"   size="mini">给学生发消息</el-button>
        <el-button type="primary" @click="messageAddTeacher()"   size="mini">给教师发消息</el-button>
      </el-form-item>
    </el-form>
    <el-table
            :data="messageData"
            row-key="id"
            default-expand-all
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
            style="width: 100%">
      <el-table-column
              prop="send_user_name"
              label="发送人"
              width="180">
      </el-table-column>
      <el-table-column
              prop="content"
              label="内容"
              width="180">
      </el-table-column>
      <el-table-column
              prop="created_at"
              label="发送日期">
      </el-table-column>
      <el-table-column
              prop="status"
              label="状态">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status==1">已读</el-tag>
          <el-tag v-if="scope.row.status==0" >未读</el-tag>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              width="100">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status==0"  size="mini"  @click="readComplete(scope.row)" type="text" >已读</el-button>
          <el-button   size="mini"  @click="messageReplay(scope.row)" type="text" >回复</el-button>
        </template>
      </el-table-column>
    </el-table>
    <MessageAdd  ref="messageAdd" @func="addMessageBack" ></MessageAdd>
    <MessageReply  ref="messageReply" @func="replyMessageBack"  ></MessageReply>
  </el-dialog>

</template>

<script>
  import MessageAdd from './MessageAdd'
  import MessageReply from './MessageReply'

  export default {
    components: {
      MessageAdd,
      MessageReply
    },
    data() {
      return {
        ruleForm: {
          name: '',
        },
        rules: {
          name: [
            { required: true, message: '请填写学校名称', trigger: 'change' }
          ],
        },
        visible:false,
        messageData:[],
        pageSize:10,
        currentPage:0,
        pageTotal:0,
        send_type:''
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              name:this.ruleForm.name
            }
            var url = "http://www.myteach.com/api/addStudent";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }else{
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
              }
            }).catch((error)=> {
              this.loading = false;
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      getMessageData(){
        var param = {
          pageSize: this.pageSize,
          page: this.currentPage,
          type:this.send_type
        };
        var url = "http://www.myteach.com/api/getMessageData";
        this.axios.post(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSize=res.data.data.pageSize
            this.pageTotal = res.data.data.total
            this.messageData=res.data.data.data
            this.messageData.forEach((value)=>{
              value.send_user_name=value.send_user.name
            })

          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      readComplete(row){
        var param = {
          id: row.id,
          type:this.send_type
        };
        var url = "http://www.myteach.com/api/updateMessageStatus";
        this.axios.post(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {
            this.$message({
              message: res.data.msg,
              type: "error"
            });
            return false;
          }else{
            this.$message({
              message: res.data.msg,
              type: "success"
            });
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      messageAddStudent(){
        this.$refs.messageAdd.initData()
        this.$refs.messageAdd.visible=true
        this.$refs.messageAdd.send_type=this.send_type
        this.$refs.messageAdd.receive_type='student'

        this.$refs.messageAdd.getStudentsByUserId()
      },
      messageAddTeacher(){
        this.$refs.messageAdd.initData()
        this.$refs.messageAdd.visible=true
        this.$refs.messageAdd.send_type=this.send_type
        this.$refs.messageAdd.receive_type='teacher'
        this.$refs.messageAdd.users=[]
        this.$refs.messageAdd.getTeachersByUserId()
      },
      messageReplay(row){
        this.$refs.messageReply.visible=true
        this.$refs.messageReply.user_type=this.send_type
        this.$refs.messageReply.edit(row)
      },
      addMessageBack(){
        this.getMessageData()
      },
      replyMessageBack(){
          this.getMessageData()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



