<template>
  <div class="hello">
    <div>
      <el-tag  type="primary"   style="margin-bottom:15px;">验证成功</el-tag>
    </div>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">点击前往登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          email: '',
          password: '',
        },
        rules: {
          email: [
            { required: true, message: '请填写邮箱', trigger: 'change' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'change' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        window.open("http://localhost:8080/#/login")
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



